.inputs {
    padding: 100px;
  }

.tagInput {
    margin: auto;
    margin-left: 10px;
    margin-bottom: 10px;
    border: none;
    padding: 0;
    width: 12ch;
    background: repeating-linear-gradient(90deg, #696969 0, #696969 1ch, transparent 0, transparent 1.5ch) 0 100%/11.5ch 2px no-repeat;
    font: 5ch droid sans mono, consolas, monospace;
    letter-spacing: 0.5ch;
    color: #ffffff;
  }
  
  .tagInput::placeholder {
    color: #B4B4B4;
  }
  
  .tagInput:focus {
    outline: none;
    color: #ffffff;
  }
  
  .inputButton {
    padding: 10px;
    width: 240px;
    margin: 5px;
    background-color: #2d313a;
    border: none;
    color: #B4B4B4;
    border-radius: 5px;
    font-family: Rubik,sans-serif;
    font-size: 25px;
  }
  
  .inputButton:hover {
    background-color: #3d4149;
  }